:root {
    --dark-blue: #086E7D;
    --white: #f4f4f4;
    --white-filter: invert(99%) sepia(23%) saturate(66%) hue-rotate(260deg) brightness(112%) contrast(91%);
    /* --black: #282846; */
    --black: #131313;
    --yellow: #FFC900;
    --neon: #00dcbf;
    --neon-transparent: rgb(0, 220, 191, 0.5);
    --neon-filter: invert(61%) sepia(99%) saturate(1523%) hue-rotate(125deg) brightness(99%) contrast(101%);
    
    --shadow: 0px 2px 5px rgba(0, 0, 0, 0.5);
    --shadow-hover: 0px 3px 7px rgba(0, 0, 0, 0.6);
    --heading-margin: 10px 0;
    --button-icon-font-family: 'Material Symbols Sharp';
    --button-icon-font-size: 120%;
    --button-icon-margin: auto 10px;
    --big-border-width: 5px;
    --hover-transition-duration: 0.2s;
    /* --border-radius: 5px; */
    --border-radius: 0px;
    --top-border-thickness: 20px;

    --font-monospace: 'Space Mono', monospace;
    --font-serif: 'Poppins', sans-serif;
}

html {
    scroll-behavior: smooth;
}

body {
    font-family: var(--font-serif);
    transition-duration: var(--hover-transition-duration);
}

h1, h2, h3, h4, h5, h6 {
    /* font-family: 'Space Grotesk', sans-serif; */
    font-family: var(--font-monospace);
    padding: var(--heading-margin);
}

.code {
    font-family: var(--font-monospace);
}

button {
    width: 150px;
    height: 50px;
    margin: 10px;
    border-style: solid;
    /* border-radius: var(--border-radius); */
    border-width: 0;/*3px;*/
    border-color: var(--dark-blue);
    /* box-shadow: var(--shadow); */
    font-size: 100%;
    font-family: var(--font-monospace);
    transition-duration: var(--hover-transition-duration);
}

button:hover {
    cursor: pointer;
}

h2 {
    font-size: 300%;
    font-weight: 400;
    /* font-style: italic; */
    text-align: center;
    
}

h3 {
    font-size: 200%;
    /* font-style: oblique; */
}

h4 {
    font-size: 150%;
}

p {
    line-height: 1.5;
}

strong {
    font-weight: 600;
}

section ul {
    list-style-type: disc;
    margin-left: 20px;
}

li {
    margin: 10px 0;
}

.highlight,
.highlight-dark {
    position: relative;
    isolation: isolate;
    display: inline-block;
    /* font-style: italic; */
    /* font-family: 'Space Grotesk', sans-serif; */
}

.highlight::before,
.highlight-dark::before {
    content: "";
    width: 100%;
    height: 10px;
    position: absolute;
    left: 0px;
    bottom: 0px;
    z-index: -1;
    transform: skew(-0.312rad);
}

.highlight::before {
    background-color: var(--yellow);
}

.highlight-dark::before {
    background-color: var(--dark-blue);
}

.material-symbols-sharp {
  font-variation-settings:
  'FILL' 0,
  'wght' 400,
  'GRAD' 0,
  'opsz' 24
}